<template>
    <div class="MessageView">
        <ClButton @click="openMessage('info')">info</ClButton>
        <ClButton @click="openMessage('success')">success</ClButton>
        <ClButton @click="openMessage('warning')">warning</ClButton>
        <ClButton @click="openMessage('error')">error</ClButton>
        <ClButton @click="openMessage('loading')">loading</ClButton>


        <div class="viewDes">
            <h3>API</h3>
            <h4>message props</h4>
            <p></p>
            <ul>
                <li>this.$Message.info(config)</li>
                <li>this.$Message.success(config)</li>
                <li>this.$Message.warning(config)</li>
                <li>this.$Message.error(config)</li>
                <li>this.$Message.loading(config)</li>
            </ul>

            <p>configdurationconfig</p>
            <div class="propItem">
                <p><b>message</b></p>
                <p><b>String</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>duration</b></p>
                <p><b>Number</b></p>
                <p><b></b>0</p>
                <p><b></b>3000</p>
            </div>
            <div class="propItem">
                <p><b>top</b></p>
                <p><b>Number</b></p>
                <p><b></b>px</p>
                <p><b></b>20</p>
            </div>
            <div class="propItem">
                <p><b>onClose</b></p>
                <p><b>Function</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
        </div>
    </div>
</template>

<script>
  import ClButton from 'cl-ui/packages/components/button/src/button.vue'
  export default {
    name: "MessageView",
    data() {
      return {}
    },
    computed: {},
    components: {
      ClButton
    },
    created() {
    },
    mounted() {
    },
    methods: {
      openMessage(type){
        // this.$Message[type]('this is a message', 5000);
        let a = this.$Message[type]({
          top: 50,
          duration: 0,
          message: 'this is a config message',
          onClose(){
            console.log('close--')
          }
        });
        setTimeout(()=>{
          a.close();
        },5000)
      }
    }
  }
</script>
<style lang="scss">
    .MessageView{
        text-align: left;
    }
</style>
