<template>
    <div class="checkboxView">
        <div>
            <h4></h4>
            <ClCheckbox v-model="checkboxBase" @change="checkboxChange"></ClCheckbox>

            <h4>disabled</h4>
            <ClCheckbox v-model="checkboxDisabled" disabled @change="checkboxChange">disabled</ClCheckbox>

            <h4>true-valuefalse-value</h4>
            <ClCheckbox v-model="checkboxCustom" @change="checkboxChange" trueValue="1" falseValue="0">true-valuefalse-value</ClCheckbox>

            <h4>size</h4>
            <ClCheckbox v-model="checkboxSmall" @change="checkboxChange" size="small">small</ClCheckbox>
            <ClCheckbox v-model="checkboxDefault" @change="checkboxChange">default</ClCheckbox>
            <ClCheckbox v-model="checkboxLarge" @change="checkboxChange" size="large">large</ClCheckbox>

            <h4>indeterminate</h4>
            <ClCheckbox v-model="checkboxIndeterminate" indeterminate @change="checkboxChange">indeterminate</ClCheckbox>

            <h4>--option</h4>
            <ClCheckboxGroup v-model="checkboxGroupOptionValue" :options="checkboxGroupOption" @change="checkboxChange"></ClCheckboxGroup>

            <h4>--slot</h4>
            <ClCheckboxGroup v-model="checkboxGroupSlotValue" @change="checkboxChange">
                <ClCheckbox label="checkbox1">Group1</ClCheckbox>
                <ClCheckbox label="checkbox2">Group2</ClCheckbox>
                <ClCheckbox label="checkbox3">Group3</ClCheckbox>
                <ClCheckbox label="checkbox4">Group4</ClCheckbox>
            </ClCheckboxGroup>

            <h4>--disabled</h4>
            <ClCheckboxGroup v-model="checkboxGroupDisabled" disabled @change="checkboxChange">
                <ClCheckbox label="checkbox1">Group1</ClCheckbox>
                <ClCheckbox label="checkbox2">Group2</ClCheckbox>
                <ClCheckbox label="checkbox3">Group3</ClCheckbox>
                <ClCheckbox label="checkbox4">Group4</ClCheckbox>
            </ClCheckboxGroup>

            <h4>--disabled</h4>
            <ClCheckboxGroup v-model="checkboxGroupSingleDisabled" @change="checkboxChange">
                <ClCheckbox label="checkbox1">Group1</ClCheckbox>
                <ClCheckbox label="checkbox2" disabled>Group2</ClCheckbox>
                <ClCheckbox label="checkbox3">Group3</ClCheckbox>
                <ClCheckbox label="checkbox4">Group4</ClCheckbox>
            </ClCheckboxGroup>

            <h4></h4>
            <ClCheckbox v-model="checkboxAll" :indeterminate="indeterminateAll" @change="checkboxChangeAll"></ClCheckbox>
            <br>
            <ClCheckboxGroup v-model="checkboxGroupAll" :options="checkboxGroupOption" @change="checkboxChangeAllChildren"></ClCheckboxGroup>
        </div>

        <div class="viewDes">
            <h3>API</h3>
            <h4>checkbox props</h4>

            <div class="propItem">
                <p><b>value</b></p>
                <p><b>Number, String, Boolean</b></p>
                <p><b></b>v-modeltrue-valuefalse-valueboolean</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>label</b></p>
                <p><b>Number, String, Boolean</b></p>
                <p><b></b>optionsslotcheckbox</p>
                <p><b></b></p>
            </div>
            <div class="propItem">
                <p><b>indeterminate</b></p>
                <p><b>Boolean</b></p>
                <p><b></b>indeterminate</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>disabled</b></p>
                <p><b>Boolean</b></p>
                <p><b></b>true/false</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>name</b></p>
                <p><b>String</b></p>
                <p><b></b>name</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>trueValue</b></p>
                <p><b>Number, String, Boolean</b></p>
                <p><b></b></p>
                <p><b></b>true</p>
            </div>
            <div class="propItem">
                <p><b>falseValue</b></p>
                <p><b>Number, String, Boolean</b></p>
                <p><b></b></p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>size</b></p>
                <p><b>String</b></p>
                <p><b></b>checkboxsmall/default/large</p>
                <p><b></b>default</p>
            </div>
        </div>

        <div class="viewDes">
            <h3>API</h3>
            <h4>checkboxGroup props</h4>

            <div class="propItem">
                <p><b>value</b></p>
                <p><b>Array</b></p>
                <p><b></b>v-modelcheckboxlabeloptionsvalue</p>
                <p><b></b>[]</p>
            </div>
            <div class="propItem">
                <p><b>disabled</b></p>
                <p><b>Boolean</b></p>
                <p><b></b>checkbox</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>size</b></p>
                <p><b>String</b></p>
                <p><b></b>checkbox</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>options</b></p>
                <p><b>Array</b></p>
                <p><b></b>[{value: '0', label: 'checkbox'}]valuelabel</p>
                <p><b></b>[]</p>
            </div>
        </div>
    </div>
</template>

<script>

  export default {
    name: "checkboxView",
    data() {
      return {
        checkboxBase: false,
        checkboxDisabled: false,
        checkboxCustom: '0',
        checkboxSmall: false,
        checkboxDefault: false,
        checkboxLarge: false,
        checkboxIndeterminate: false,

        checkboxGroupOptionValue: ['value1'],
        checkboxGroupOption: [
          {
            label: '1',
            value: 'value1'
          },
          {
            label: '2',
            value: 'value2'
          },
          {
            label: '3',
            value: 'value3'
          },
          {
            label: '4',
            value: 'value5'
          },
        ],

        checkboxGroupSlotValue: [],

        checkboxGroupDisabled: [],

        checkboxGroupSingleDisabled: [],

        checkboxAll: false,
        indeterminateAll: false,
        checkboxGroupAll: [],
      }
    },
    computed: {},
    components: {
    },
    created() {
    },
    mounted() {
    },
    methods: {
      checkboxChange(value){
        console.log(value, 'vvvcheckboxvvv')
      },
      checkboxChangeAllChildren(){
        if(this.checkboxGroupAll.length === this.checkboxGroupOption.length){
          this.checkboxAll = true;
        }else if(this.checkboxGroupAll.length && this.checkboxGroupAll.length !== this.checkboxGroupOption.length){
          this.indeterminateAll = true;
          this.checkboxAll = false;
        }else{
          this.checkboxAll = false;
          this.indeterminateAll = false;
        }
      },
      checkboxChangeAll(){
        if(this.checkboxAll){
          this.checkboxGroupOption.forEach(item=>{
            this.checkboxGroupAll.push(item.value)
          })
        }else{
          this.checkboxGroupAll = [];
          this.indeterminateAll = false;
        }
      }
    }
  }
</script>

