<template>
    <div class="UploadView">
        <ClUpload action="//jsonplaceholder.typicode.com/posts/" :file-list="fileListBase">
            <ClButton>upload</ClButton>
        </ClUpload>

        <ClUpload action="//jsonplaceholder.typicode.com/posts/" listType="picture" :file-list="fileListBase">
            <ClButton>upload</ClButton>
        </ClUpload>

        <ClUpload action="//jsonplaceholder.typicode.com/posts/" listType="picture-card"
                  :file-list="fileListBase" :onPreview="handlerPreview" :onSuccess="handlerSuccess">
            <i class="cl-icon-plus"></i>
            <div></div>
        </ClUpload>


        <ClUpload action="//jsonplaceholder.typicode.com/posts/" listType="picture-card" :file-list="fileListBase" :onPreview="handlerPreview" drag>
            <i class="cl-icon-plus"></i>
            <div></div>
        </ClUpload>

        <p>accpect</p>
        <ClUpload action="//jsonplaceholder.typicode.com/posts/"
                  accept=".png"
                  :format="['png']"
                  :file-list="fileListBase"
                  :onError="handlerError"
                  :onExceed="handlerExceed"
                  :onProgress="handlerProgress"
                  :onSuccess="handlerSuccess"
                  :onChange="handlerChange"
                  :beforeUpload="handlerBeforeUpload"
                  :onRemove="handlerRemove">
            <ClButton>upload</ClButton>
        </ClUpload>


        <ClModal v-model="modalVisible" footer-hide>
            <img :src="modalFile.url" width="100%" alt="">
        </ClModal>


        <div class="viewDes">
            <h3>API</h3>
            <h4>upload props</h4>

            <div class="propItem">
                <p><b>name</b></p>
                <p><b>String</b></p>
                <p><b></b></p>
                <p><b></b>file</p>
            </div>
            <div class="propItem">
                <p><b>action</b></p>
                <p><b>String</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>listType</b></p>
                <p><b>String</b></p>
                <p><b></b>text/picture/picture-card</p>
                <p><b></b>text</p>
            </div>
            <div class="propItem">
                <p><b>fileList</b></p>
                <p><b>Array</b></p>
                <p><b></b>fileList[{name: 'name', url: ''}]</p>
                <p><b></b>[]</p>
            </div>
            <div class="propItem">
                <p><b>drag</b></p>
                <p><b>Boolean</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>multiple</b></p>
                <p><b>Boolean</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>accept</b></p>
                <p><b>String</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>format</b></p>
                <p><b>Array</b></p>
                <p><b></b>acceptacceptaccept['jpg','png'],</p>
                <p><b></b>[]</p>
            </div>
            <div class="propItem">
                <p><b>showUploadList</b></p>
                <p><b>Boolean</b></p>
                <p><b></b></p>
                <p><b></b>true</p>
            </div>
            <div class="propItem">
                <p><b>limit</b></p>
                <p><b>Number</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>maxSize</b></p>
                <p><b>Number</b></p>
                <p><b></b>KB</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>disabled</b></p>
                <p><b>Boolean</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>headers</b></p>
                <p><b>Object</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>data</b></p>
                <p><b>Object</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>beforeUpload</b></p>
                <p><b>Function(file)</b></p>
                <p><b></b>falsePromise</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>onChange</b></p>
                <p><b>Function(file, fileList)</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>onProgress</b></p>
                <p><b>Function(event, file, fileList)</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>onSuccess</b></p>
                <p><b>Function(response, file, fileList)</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>onError</b></p>
                <p><b>Function(error, file, fileList)</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>onRemove</b></p>
                <p><b>Function(file, fileList)</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>onPreview</b></p>
                <p><b>Function(file)</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>onExceed</b></p>
                <p><b>Function(file, fileList)</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
        </div>

        <div class="viewDes">
            <h3>API</h3>
            <h4>upload slot</h4>

            <div class="propItem">
                <p><b>-</b></p>
                <p><b></b></p>
            </div>
            <div class="propItem">
                <p><b>tip</b></p>
                <p><b></b></p>
            </div>
        </div>
    </div>
</template>

<script>
  import ClUpload from 'cl-ui/packages/components/upload/src/upload.vue'

  export default {
    name: "UploadView",
    data() {
      return {
        modalVisible: false,
        modalFile: {},
        fileListBase: [
          {
            name: '1.png',
            url: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2300160417,2710453054&fm=111&gp=0.jpg'
          },
          {
            name: 'cesihaindhisdiandjaidiuadjiandandanjndjsandkjnaknda.jpg',
            url: 'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3796581224,2787909871&fm=111&gp=0.jpg'
          },
          {
            name: '2.gif',
            url: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4168008361,363981833&fm=111&gp=0.jpg'
          }
        ]
      }
    },
    computed: {},
    components: {
      ClUpload
    },
    created() {
    },
    mounted() {
    },
    methods: {
      handlerPreview(file) {
        this.modalFile = file;
        this.modalVisible = true;
      },
      handlerError(err, response, file) {
        console.log(err, response, file, 'handlerError')
      },
      handlerExceed(file, fileList) {
        console.log(file, JSON.parse(JSON.stringify(fileList)), 'handlerExceed')
      },
      handlerRemove(file, fileList) {
        console.log(file, JSON.parse(JSON.stringify(fileList)), 'handlerRemove')
      },
      handlerProgress(e, file, fileList) {
        console.log(e, file, JSON.parse(JSON.stringify(fileList)), 'handlerProgress')
      },
      handlerSuccess(res, file, fileList) {
        console.log(res, file, JSON.parse(JSON.stringify(fileList)), 'handlerSuccess');
        // this.fileListBase.push({
        //   name: res.name,
        //   url: res.url
        // });
      },
      handlerChange(file, fileList) {
        console.log(file, JSON.parse(JSON.stringify(fileList)), 'handlerChange')
      },
      handlerBeforeUpload(file) {
        console.log(file, 'handlerBeforeUpload');
        return true;
      },
    }
  }
</script>

