<template>
    <div class="BadgeView">
        <h4></h4>
        <ClBadge :count="badgeValue" :showZero="badgeshowZero" :dot="badgeDot">
            <ClButton></ClButton>
        </ClBadge>
        <br>
        <ClButtonGroup>
            <ClButton type="primary" icon="cl-icon-plus" @click="handlerClick('add')"></ClButton>
            <ClButton type="primary" icon="cl-icon-minus" @click="handlerClick('minus')"></ClButton>
        </ClButtonGroup>
        <ClSwitch v-model="badgeshowZero" width="120px">
            <span slot="open">showZero=true</span>
            <span slot="close">showZero=false</span>
        </ClSwitch>
        <ClSwitch v-model="badgeDot" width="80px">
            <span slot="open">dot=true</span>
            <span slot="close">dot=false</span>
        </ClSwitch>

        <h4></h4>
        <ClBadge :count="10">
            <ClButton></ClButton>
        </ClBadge>
        <br>
        <ClBadge :count="150">
            <ClButton></ClButton>
        </ClBadge>

        <h4>dot</h4>
        <ClBadge :count="150" dot>
            <ClButton>dot</ClButton>
        </ClBadge>

        <h4>text</h4>
        <ClBadge :count="150" text="new">
            <ClButton>Text</ClButton>
        </ClBadge>

        <h4></h4>
        <ClBadge :count="10"></ClBadge>
        <ClBadge :count="10" dot></ClBadge>

        <h4></h4>
        <ClBadge :count="150" color="primary">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" color="error">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" color="success">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" color="warning">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" color="info">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" dot color="primary">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" dot color="error">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" dot color="success">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" dot color="warning">
            <ClButton>Text</ClButton>
        </ClBadge>
        <ClBadge :count="150" dot color="info">
            <ClButton>Text</ClButton>
        </ClBadge>
        <h4></h4>
        <ClBadge :count="150" color="#c8c8c8">
            <ClButton>#c8c8c8</ClButton>
        </ClBadge>
        <ClBadge :count="150" color="#ffa2d3">
            <ClButton>#ffa2d3</ClButton>
        </ClBadge>
        <h4></h4>
        <ClBadge text="150" color="#c8c8c8"></ClBadge>
        <h4>status</h4>
        <ClBadge status="success" text="success"></ClBadge>
        <br>
        <ClBadge status="warning" text="warning"></ClBadge>
        <br>
        <ClBadge status="error" text="error"></ClBadge>
        <br>
        <ClBadge status="default" text="default"></ClBadge>
        <br>
        <ClBadge status="processing" text="processing"></ClBadge>
        <h4>offset</h4>
        <ClBadge :count="20" :offset="[20, 0]">
            <ClButton>offset</ClButton>
        </ClBadge>


        <div class="viewDes">
            <h3>API</h3>
            <h4>badge props</h4>

            <div class="propItem">
                <p><b>count</b></p>
                <p><b>Number</b></p>
                <p><b></b>-</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>overflowCount</b></p>
                <p><b>Number</b></p>
                <p><b></b>-</p>
                <p><b></b>99</p>
            </div>
            <div class="propItem">
                <p><b>dot</b></p>
                <p><b>Boolean</b></p>
                <p><b></b>true/false</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>showZero</b></p>
                <p><b>Boolean</b></p>
                <p><b></b>true/false</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>text</b></p>
                <p><b>String</b></p>
                <p><b></b>status</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>status</b></p>
                <p><b>String</b></p>
                <p><b></b>success/error/warning/processing/default</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>color</b></p>
                <p><b>String</b></p>
                <p><b></b>badge</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>offset</b></p>
                <p><b>Array</b></p>
                <p><b></b>badge[x,y]</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>title</b></p>
                <p><b>String</b></p>
                <p><b></b>badgetextcount</p>
                <p><b></b>-</p>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: "BadgeView",
    data() {
      return {
        badgeValue: 10,
        badgeshowZero: false,
        badgeDot: false,
      }
    },
    computed: {},
    components: {},
    created() {
    },
    mounted() {
    },
    methods: {
      handlerClick(type){
        type === 'add' ? this.badgeValue += 10 : this.badgeValue -= 10;
      }
    }
  }
</script>
