<template>
    <div class="radioView">
        <div>
            <h4>--label--</h4>
            <ClRadio v-model="radioBase" @change="changHandler"></ClRadio>

            <h4>--label--</h4>
            <ClRadio v-model="radioBaseLable" label="1" @change="changHandler">Label</ClRadio>

            <h4>disabled--</h4>
            <ClRadio v-model="radioDisabled" disabled @change="changHandler"></ClRadio>

            <h4>size--</h4>
            <ClRadio v-model="radioSize" label="small" size="small" @change="changHandler">small</ClRadio>
            <ClRadio v-model="radioSize" label="default" @change="changHandler">default</ClRadio>
            <ClRadio v-model="radioSize" label="large" size="large" @change="changHandler">large</ClRadio>

            <h4>--options</h4>
            <ClRadioGroup v-model="radioGroupOptionValue" :options="radioOptions" @change="changHandler"></ClRadioGroup>

            <h4>--slot</h4>
            <ClRadioGroup v-model="radioGroupSlotValue" @change="changHandler">
                <ClRadio label="0">radio0</ClRadio>
                <ClRadio label="1">radio1</ClRadio>
                <ClRadio label="2">radio2</ClRadio>
                <ClRadio label="3">radio3</ClRadio>
                <ClRadio label="4">radio4</ClRadio>
            </ClRadioGroup>

            <h4>--size</h4>
            <ClRadioGroup v-model="radioGroupOptionValueSizeSmall" size="small" :options="radioOptions" @change="changHandler"></ClRadioGroup>
            <ClRadioGroup v-model="radioGroupOptionValueSizeDefault" :options="radioOptions" @change="changHandler"></ClRadioGroup>
            <ClRadioGroup v-model="radioGroupOptionValueSizeLarge" size="large" :options="radioOptions" @change="changHandler"></ClRadioGroup>

            <h4>--disabled</h4>
            <ClRadioGroup v-model="radioGroupOptionValueDisabled" disabled :options="radioOptions" @change="changHandler"></ClRadioGroup>

            <h4>--disabled</h4>
            <ClRadioGroup v-model="radioGroupSlotValueSingleDisabled" @change="changHandler">
                <ClRadio label="0" disabled>radio0--disabled</ClRadio>
                <ClRadio label="1">radio1</ClRadio>
                <ClRadio label="2">radio2</ClRadio>
                <ClRadio label="3">radio3</ClRadio>
                <ClRadio label="4">radio4</ClRadio>
            </ClRadioGroup>
        </div>

        <div class="viewDes">
            <h3>API</h3>
            <h4>radio props</h4>

            <div class="propItem">
                <p><b>value</b></p>
                <p><b>Number, String, Boolean</b></p>
                <p><b></b>v-modellabeltruelabellabel</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>label</b></p>
                <p><b>Number, String, Boolean</b></p>
                <p><b></b>optionsslot</p>
                <p><b></b>true</p>
            </div>
            <div class="propItem">
                <p><b>disabled</b></p>
                <p><b>Boolean</b></p>
                <p><b></b>true/false</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>name</b></p>
                <p><b>String</b></p>
                <p><b></b>name</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>size</b></p>
                <p><b>String</b></p>
                <p><b></b>checkboxsmall/default/large</p>
                <p><b></b>default</p>
            </div>
        </div>

        <div class="viewDes">
            <h3>API</h3>
            <h4>radioGroup props</h4>

            <div class="propItem">
                <p><b>value</b></p>
                <p><b>String, Number, Boolean</b></p>
                <p><b></b>radiov-model</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>disabled</b></p>
                <p><b>Boolean</b></p>
                <p><b></b>radio</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>size</b></p>
                <p><b>String</b></p>
                <p><b></b>radio</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>options</b></p>
                <p><b>Array</b></p>
                <p><b></b>[{value: '0', label: 'checkbox'}]valuelabel</p>
                <p><b></b>[]</p>
            </div>
        </div>
    </div>
</template>

<script>


  import ClRadio from 'cl-ui/packages/components/radio/src/radio.vue'
  import ClRadioGroup from 'cl-ui/packages/components/radio/src/radio-group.vue'
  export default {
    name: "checkboxView",
    data() {
      return {
        radioBase: false,
        radioBaseLable: '',
        radioDisabled: false,
        radioSize: '',

        radioGroupOptionValue: '',
        radioOptions: [
          {
            value: '0',
            label: '1'
          },
          {
            value: '1',
            label: '2'
          },
          {
            value: '2',
            label: '3'
          },
          {
            value: '3',
            label: '4'
          }
        ],

        radioGroupSlotValue: '',

        radioGroupOptionValueSizeSmall: '',
        radioGroupOptionValueSizeDefault: '',
        radioGroupOptionValueSizeLarge: '',

        radioGroupOptionValueDisabled: '',

        radioGroupSlotValueSingleDisabled: '',
      }
    },
    computed: {},
    components: {
      ClRadio,
      ClRadioGroup,
    },
    created() {
    },
    mounted() {
    },
    methods: {
      changHandler(value){
        console.log(value, 'vvvvvv')
      },
    }
  }
</script>

