<script>
    import ClModal from 'cl-ui/packages/components/modal/src/modal.vue'
    import ClButton from 'cl-ui/packages/components/button/src/button.vue'

  export default {
    name: "ModalView",
    data() {
      return {
        modalValueBase: false,
        modalValueWidth: false,
        modalValueTop: false,
        modalValueRenderHtml: false,
        modalValueLoading: false,
        modalValueTransition: false,
        modalValueBodyScroll: false,
        modalValueFullscreen: false,
        modalValueSlotsHeaderOrFooter: false,
        modalValueFooterHide: false,
        modalValueHeader: false,
        modalValueClose: false,
        modalValueButtonText: false,
        modalValueCover: false,
        modalValueNoCover: false,

        loading: true
      }
    },
    computed: {},
    components: {
      ClModal,
      ClButton
    },
    created() {
    },
    mounted() {

    },
    methods: {
      openModal(type){
        this[`modalValue${type}`] = true;
      },
      handlerOk(type){
        if(type === 'Loading'){
          setTimeout(()=>{
            this[`modalValue${type}`] = false;
          },2000);
        }else{
          this[`modalValue${type}`] = false;
        }
      },
      installModal(type){
        this.$Modal[type]({
          title: `${type}`,
          content: ['','',''],
          loading: type === 'confirm',
          onOk(){
            if(type === 'confirm'){
              setTimeout(()=>{
                this.$Modal.remove();
              },2000);
            }
            console.log('====ok')
          },
          onCancel(){

          }
        })
      }
    }
  }
</script>
