<template>
    <div class="SliderView">
        normal:<ClSlider v-model="sliderValue" @change="handlerChange"></ClSlider>
        railColor:<ClSlider v-model="sliderValue" railColor="#ccc" disabled  @change="handlerChange"></ClSlider>
        barColor:<ClSlider v-model="sliderValue" barColor="red" disabled  @change="handlerChange"></ClSlider>
        always:<ClSlider v-model="sliderValueAlways" tooltipVisible="always"  @change="handlerChange"></ClSlider>
        never:<ClSlider v-model="sliderValueNever" tooltipVisible="never"  @change="handlerChange"></ClSlider>
        disabled:<ClSlider v-model="sliderValueDis" disabled  @change="handlerChange"></ClSlider>
        step=0.1:<ClSlider v-model="sliderValueStep" :step="0.1"  @change="handlerChange"></ClSlider>
        min:10,max:80:<ClSlider v-model="sliderValue1" :min="10" :max="80"  @change="handlerChange"></ClSlider>
        tipFormat:<ClSlider v-model="sliderValueFormat" :tipFormat="tipFormat"  @change="handlerChange"></ClSlider>

        <p>vertical</p>
        <div class="vertical">
            <ClSlider v-model="sliderValueVer" vertical  @change="handlerChange"></ClSlider>
            step=10:<ClSlider v-model="sliderValueVerStep" vertical :step="10"  @change="handlerChange"></ClSlider>
        </div>


        <div class="viewDes">
            <h3>API</h3>
            <h4>slider props</h4>

            <div class="propItem">
                <p><b>value</b></p>
                <p><b>Array, Number</b></p>
                <p><b></b>Arrayv-model</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>min</b></p>
                <p><b>Number</b></p>
                <p><b></b></p>
                <p><b></b>0</p>
            </div>
            <div class="propItem">
                <p><b>max</b></p>
                <p><b>Number</b></p>
                <p><b></b></p>
                <p><b></b>100</p>
            </div>
            <div class="propItem">
                <p><b>step</b></p>
                <p><b>Number</b></p>
                <p><b></b>(max-min)</p>
                <p><b></b>1</p>
            </div>
            <div class="propItem">
                <p><b>range</b></p>
                <p><b>Boolean</b></p>
                <p><b></b></p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>disabled</b></p>
                <p><b>Boolean</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>vertical</b></p>
                <p><b>Boolean</b></p>
                <p><b></b>slider</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>tooltipVisible</b></p>
                <p><b>String</b></p>
                <p><b></b>tooltiphover/always/neverhoveralwaysnever</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>tipFormat</b></p>
                <p><b>Function</b></p>
                <p><b></b>tooltip</p>
                <p><b></b>value</p>
            </div>
            <div class="propItem">
                <p><b>railColor</b></p>
                <p><b>String</b></p>
                <p><b></b>rail</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>barColor</b></p>
                <p><b>String</b></p>
                <p><b></b>bar</p>
                <p><b></b>-</p>
            </div>
        </div>

        <div class="viewDes">
            <h3>API</h3>
            <h4>switch event</h4>

            <div class="propItem">
                <p><b>change</b></p>
                <p><b></b></p>
                <p><b></b>value</p>
            </div>
        </div>
    </div>
</template>

<script>
  import ClSlider from 'cl-ui/packages/components/slider/src/slider.vue'
  export default {
    name: "SliderView",
    data() {
      return {
        sliderValue: 10,
        sliderValueAlways: 10,
        sliderValueNever: 10,
        sliderValueStep: 10,
        sliderValueDis: 10,
        sliderValue1: 20,
        sliderValueFormat: 20,

        sliderValueVer: 10,
        sliderValueVerStep: 10
      }
    },
    computed: {},
    components: {
      ClSlider
    },
    created() {
    },
    mounted() {

    },
    methods: {
      tipFormat(value){
        return `${value}%`
      },
      handlerChange(value){
        console.log(value,'change')
      },
    }
  }
</script>

<style lang="scss" scoped>
    .SliderView{
        &>*{
            width: 500px;
        }
        .vertical{
            height: 200px;
            margin: 0 50px;
            display: flex;
            padding-bottom: 50px;
        }
    }

</style>
