<template>
    <vuescroll class="cl-scroll" :ops="options">
        <slot></slot>
    </vuescroll>
</template>

<script>
  import vuescroll from 'vuescroll';
  export default {
    name: "ClScroll",
    props: {
      size: String,
      scrollOption: {
        type: Object,
        default: function () {
          return {}
        }
      },
    },
    data() {
      return {
        ops: {
          vuescroll: {
            mode: 'native',
            sizeStrategy: 'percent',
            detectResize: true,
          },
          scrollPanel: {
            initialScrollY: false,//mounted
            initialScrollX: false,
            scrollingX: true,// x  y 
            scrollingY: true,
            speed: 300,
            easing: undefined,
            verticalNativeBarPos: 'right'
          },
          rail: {
            background: '#cecece',//
            opacity: 0,//
            size: '6px',//
            specifyBorderRadius: false,// borderRadius 
            gutterOfEnds: null,// x  y 
            gutterOfSide: '1px',//
            keepShow: false,// bar 
          },
          bar: {
            showDelay: 500,//
            onlyShowBarOnScroll: true,// bar
            keepShow: false,//
            background: '#c1c1c1',//
            opacity: 1,//
            hoverStyle: false,
            specifyBorderRadius: false,// borderRadius 
            minSize: false,
            size: '6px',
            disable: false,//
          }
        }
      }
    },
    computed: {
      options(){
        let ops = this.ops;
        if(this.size === 'small'){
          ops.bar.size = '4px'
        }
        return Object.assign({}, ops, this.scrollOption);
      }
    },
    components: {
      vuescroll
    },
    created() {
    },
    mounted() {
    },
    methods: {}
  }
</script>
