<template>
    <div class="SwitchView">
        <p></p>
        <ClSwitch size="small" @change="handlerChange"></ClSwitch>
        <ClSwitch v-model="defaultValue" @change="handlerChange"></ClSwitch>
        <ClSwitch size="large" @change="handlerChange"></ClSwitch>

        <p>disabled</p>
        <ClSwitch disabled @change="handlerChange"></ClSwitch>
        <ClSwitch disabled v-model="disabledValue" @change="handlerChange"></ClSwitch>

        <p>slot</p>
        <ClSwitch size="small" @change="handlerChange">
            <span slot="open"></span>
            <span slot="close"></span>
        </ClSwitch>
        <ClSwitch @change="handlerChange">
            <span slot="open"></span>
            <span slot="close"></span>
        </ClSwitch>
        <ClSwitch size="large" @change="handlerChange">
            <span slot="open"></span>
            <span slot="close"></span>
        </ClSwitch>

        <p></p>
        <ClSwitch v-model="defaultValue" checkedColor="red" @change="handlerChange"></ClSwitch>

        <p></p>
        <ClSwitch v-model="defaultValue" width="80px" @change="handlerChange">
            <span slot="open"></span>
            <span slot="close"></span>
        </ClSwitch>

        <p>loading</p>
        <ClSwitch v-model="defaultValue" :loading="true" @change="handlerChange"></ClSwitch>

        <p>trueValuefalseValue</p>
        <ClSwitch v-model="value" trueValue="1" falseValue="0" @change="handlerChange"></ClSwitch>


        <div class="viewDes">
            <h3>API</h3>
            <h4>switch props</h4>

            <div class="propItem">
                <p><b>value</b></p>
                <p><b>String, Number, Boolean</b></p>
                <p><b></b>v-model</p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>trueValue</b></p>
                <p><b>String, Number, Boolean</b></p>
                <p><b></b></p>
                <p><b></b>true</p>
            </div>
            <div class="propItem">
                <p><b>falseValue</b></p>
                <p><b>String, Number, Boolean</b></p>
                <p><b></b></p>
                <p><b></b>false</p>
            </div>
            <div class="propItem">
                <p><b>name</b></p>
                <p><b>String</b></p>
                <p><b></b>name</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>size</b></p>
                <p><b>String</b></p>
                <p><b></b>small/default/large</p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>checkedColor</b></p>
                <p><b>String</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>uncheckedColor</b></p>
                <p><b>String</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>disabled</b></p>
                <p><b>Boolean</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>width</b></p>
                <p><b>String, Number</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
            <div class="propItem">
                <p><b>loading</b></p>
                <p><b>Boolean</b></p>
                <p><b></b></p>
                <p><b></b>-</p>
            </div>
        </div>

        <div class="viewDes">
            <h3>API</h3>
            <h4>switch event</h4>

            <div class="propItem">
                <p><b>change</b></p>
                <p><b></b></p>
                <p><b></b></p>
            </div>
        </div>

        <div class="viewDes">
            <h3>API</h3>
            <h4>switch slot</h4>

            <div class="propItem">
                <p><b>open</b></p>
                <p><b></b></p>
            </div>
            <div class="propItem">
                <p><b>close</b></p>
                <p><b></b></p>
            </div>
        </div>
    </div>
</template>

<script>
    import ClSwitch from 'cl-ui/packages/components/switch/src/switch.vue'
  export default {
    name: "SwitchView",
    data() {
      return {
        disabledValue: true,
        defaultValue: true,
        value: '0'
      }
    },
    computed: {},
    components: {
      ClSwitch
    },
    created() {
    },
    mounted() {
    },
    methods: {
      handlerChange(value){
        console.log(value,'switch--')
      }
    }
  }
</script>

